.main {
    padding: 0 2%;
    text-align: center;
}

.divSearch {
    width: 100%;
    padding: 40px 0;
    border-bottom: #a1a1a1 1px solid;
    position: relative;
}

.imgMain {
    width: 70px;
    position: absolute;
    padding: 0;
    margin: -35px 0;
    left: 5%;
    color: #a1a1a1;
}

.inputSearch {
    width: 50%;
    padding: 5px 0;
    font-size: 14px;
    outline: none;
    font-style: italic;
    text-align: center;
}

.notFound {
    font-size: 20px;
    padding: 13% 0;
}

.imgResults {
    width: 10%;
    margin-bottom: 20px;
}

.divElement {
    display: inline-block;
    background-color: #c4c4c4;
    border: 1px solid #a1a1a1;
    border-radius: 20px;
    margin: 30px 4%;
    width: 40%;
}

.auth {
    font-size: 18px;
    line-height: 25px;
    padding: 5px 0;
    text-align: center;
    border-top: 1px solid #a1a1a1;
}


@media screen and (max-width: 1200px) {
    .main {
        padding: 0;
        text-align: center;
    }
    .imgMain {
        width: 55px;
        margin: -25px 0;
    }
    .inputSearch {
        padding: 4px 0;
        font-size: 14px;
    }
    .divElement {
        margin: 30px 0;
        width: 100%;
        border-radius: 0;
        border-right: none;
        border-left: none;
    }
}

@media screen and (max-width: 500px) {

    .divSearch {
        width: 100%;
        padding: 25px 0;
        text-align: right;
    }

    .imgMain {
        width: 50px;
        margin: -20px 0;
        left: 5%;
    }

    .inputSearch {
        width: 65%;
        margin-right: 5%;
    }
}