.mainForm {
    text-align: center;
}
.name {
    font-size: 25px;
    font-family: "Georgia";
    padding: 30px 0 20px 0;
}
.form {
    border: 2px inset #e2e2e2;
    font-size: 24px;
    padding: 20px 0;
    margin: 20px 20%;
}
.formExample {
    display: inline-block;
    position: relative;
}
.degree {
    font-size: 18px;
}
.form2 {
    font-size: 24px;
    padding: 20px 0;
}
.formInputs, .formInputs > div {
    display: inline;
    width: 100%;
}
.input {
    text-align: center;
    outline: none;
    padding: 7px 2px;
    width: 20%;
}
.input.error {
    border: brown solid 2px;
}
.inputIndex {
    text-align: center;
    padding: 2px 1px;
    width: 5%;
    outline: none;
}
.inputIndex.error {
    border: brown solid 2px;
}
.form3 {
    font-size: 24px;
    font-family: "Times New Roman";
    padding: 20px 0;
}
.buttons {
    padding: 20px 5% 30px 5%;
    font-size: 23px;
    color: #e2e2e2;
}
.buttonGo {
    cursor: pointer;
}
.buttonReset {
    float: right;
    cursor: pointer;
}

@media screen and (max-width: 1200px) {
    .name {
        font-size: 22px;
    }
    .form {
        font-size: 21px;
    }
    .degree {
        font-size: 14px;
    }
    .form2 {
        font-size: 21px;
    }
    .input {
        padding: 7px 0.5%;
        width: 16%;
    }
    .inputIndex {
        width: 4%;
    }
    .form3 {
        font-size: 21px;
    }
    .buttons {
        font-size: 23px;
    }
}